import React from 'react'
import Layout from "../components/layout"
import Banner from '../components/banner'
import BootstrapTable from 'react-bootstrap-table-next'
import ToolkitProvider, { Search } from 'react-bootstrap-table2-toolkit'
import paginationFactory from 'react-bootstrap-table2-paginator'
import helpers from '../components/helpers'
import { Link } from 'gatsby'
import "../styles/organization.css"
import {useTranslation} from 'react-i18next';
import SEO from "../components/seo"
import { Tab, TabList, TabPanel, Tabs } from 'react-tabs';
import Select from 'react-select';
const { SearchBar } = Search;

// Basic organization component
export default ({ pageContext: { organization, bibles, resources } }) => {

  console.log(resources[0])

  const { t, i18n } = useTranslation()

  function bibleLinkFormatter(cell, row) {
    return (<Link to={"/bibles/" + row.id }>{ cell }</Link>)
  }

  function ResourceTitleFormatter(cell, row) {
    return row.translations[0].title
  }

  const resourceLink = selectedOption => {
    window.location = selectedOption.value
  }

  function LinkListFormatter(cell, row) {
    if(cell) {
      if(typeof cell === 'object') {
        let optionList = []
        cell.forEach(function(element) {
          optionList.push({label: element.title, value: element.url})
        });

        return <Select options={optionList} onChange={resourceLink} />
      }
      return <a href={cell.url}>{cell.title}</a>
    }
    return ""
  }

  organization.name = helpers.names(organization.translations).name

  return (
      <Layout>
        <SEO title={ organization.name } lang={ i18n.language } />
        <Banner title={ organization.name }
                primaryColor={organization.primary_color}
                secondaryColor={organization.secondary_color}
                breadcrumbs={[
                  {path: '/', title: 'home'},
                  {path: '/organizations/', title: t('organizations.title')},
                  {path: '#', title: organization.name }
                ]}
        />
        <div className="row">
        <aside className="medium-3 columns organization-sidebar">
          <div className="row">
          <div className="small-4 medium-12 columns">
            {(organization.logo) ? <div className="centered" style={{maxHeight:'200px',maxWidth: '200px', marginBottom:'25px'}} dangerouslySetInnerHTML={{ __html: organization.logo}} />  : ''}
          </div>
          <div className="small-8 medium-12 columns">
          <p><b>{ t('fields.phone') }:</b> { organization.phone } </p>
          <p><b>{ t('fields.email') }:</b> { organization.email } </p>
          <p><b>{ t('fields.address') }:</b> { organization.address } </p>
          </div>
            <div className="row">
              <div className="organization-links">
                { (organization.url_donate) ? <a target="_blank" rel="noopener noreferrer" href={ organization.url_donate }>{ t('fields.donate') }</a> : '' }
                { (organization.url_facebook) ? <a target="_blank" rel="noopener noreferrer" href={ organization.url_facebook }>Facebook</a> : '' }
                { (organization.url_twitter) ? <a target="_blank" rel="noopener noreferrer" href={ organization.url_twitter }>Twitter</a> : '' }
                { (organization.url_website) ? <a target="_blank" rel="noopener noreferrer" href={ organization.url_website }>{ t('fields.website') }</a> : '' }
              </div>
            </div>
          </div>
        </aside>



          <div className="medium-9 columns">
            <Tabs defaultIndex={bibles.length !== 0 ? 0 : 1}>
              <TabList className="tab-list small-10 medium-9 centered">
                <Tab
                  disabled={bibles.length === 0}>
                  { t('bibles.title') + ' (' + bibles.length + ')' }
                </Tab>
                <Tab
                  disabled={resources.length === 0}>
                  { t('resources.title') + ' (' + resources.length + ')' }
                </Tab>
              </TabList>

              <TabPanel>
                {(bibles.length !== 0) ? <ToolkitProvider
                  keyField="id"
                  data={ bibles }
                  bordered={ false }
                  columns={ [
                    { dataField: 'id', text: 'ID', hidden: true, keyField: true },
                    { dataField: 'name', text: 'English Title', sort: true, formatter: bibleLinkFormatter },
                    { dataField: 'vname', text: 'Vernacular Title', sort: true, formatter: bibleLinkFormatter },
                    { dataField: 'date', text: 'Date', sort: true, align: 'center', classes: 'col-small-hide',headerClasses: 'col-small-hide' },
                  ] }
                  search>
                  {
                    props => (
                      <div>
                        <SearchBar { ...props.searchProps } delay={ 100 } placeholder={"Search " + bibles.length + " Bibles"} />
                        <BootstrapTable
                          pagination={ paginationFactory() }
                          { ...props.baseProps }
                        />
                      </div>
                    )
                  }
                </ToolkitProvider> : 'No Bibles have been found from this organization' }

              </TabPanel>
              <TabPanel>
                <h2>{ t('resources') }</h2>
                {(resources.length !== 0) ? <ToolkitProvider
                  keyField="id"
                  data={ resources }
                  bordered={ false }
                  columns={ [
                    { dataField: 'id', text: 'ID', hidden: true, keyField: true },
                    { dataField: 'type', text: 'Type'},
                    { dataField: 'iso', text: 'iso code'},
                    { dataField: 'name', text: 'name', formatter: ResourceTitleFormatter},
                    //{ dataField: 'organization', text: 'organization'},
                    { dataField: 'links', text: 'Links', formatter: LinkListFormatter}
                  ] }
                  search>
                  {
                    props => (
                      <div>
                        <SearchBar { ...props.searchProps } delay={ 100 } placeholder={"Search " + resources.length + " Resources"} />
                        <BootstrapTable
                          pagination={ paginationFactory() }
                          { ...props.baseProps }
                        />
                      </div>
                    )
                  }
                </ToolkitProvider> : 'No Resources have been found from this organization' }
              </TabPanel>
            </Tabs>
          </div>
        </div>
      </Layout>)
}
